import Modal from './Modal';

/**
 * Modal Component.
 * React wrapper for a Modal Window. Allows to dynamically toggle a Modal window.
 *
 * ================================================
 * Usage:
 *
 * =================================================
 *
 */

class FormModal extends React.Component {
  constructor() {
    super();
    this.closeModal = this.closeModal.bind(this);
    this.confirmClose = this.confirmClose.bind(this);
  }

  closeModal() {
    this.props.closeAction instanceof Function && this.props.closeAction();
    this.props.toggleModal();
  }

  confirmClose() {
    if (this.props.throwWarning) {
      swal({
        title: "Are You Sure?",
        text: "Leaving the form will result in the loss of any information entered.",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: 'Proceed',
        cancelButtonText: 'Cancel',
      },
      function() {
        this.closeModal();
      }.bind(this));
    } else {
      this.closeModal();
    }
  }

  render() {
    return (
      <Modal 
        closeModal={this.confirmClose} 
        show={this.props.show}
        {...this.props} 
      >
        {this.props.children}
      </Modal>
    );
  }
}

FormModal.propTypes = {
  closeAction: React.PropTypes.func,
  togglemodal: React.PropTypes.func.isRequired,
  throwWarning: React.PropTypes.bool.isRequired,
};

FormModal.defaultProps = {
  throwWarning: true,
}

export default FormModal;



